<template>
  <div>
    <router-view></router-view>
    <van-tabbar bind:change="onChange">
      <van-tabbar-item
        v-for="(item,index) in navBotton"
        :key="index"
        replace
        :to="{name:item.name}"
        @click="iconActive(item)"
      >
        <span :class="['iconfont', item.isActive?item.opIcon:item.unIcon]"></span>
        {{item.title}}
      </van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 一级路由信息
      navBotton: {
        home: {
          name: "Home",
          title: "首页",
          unIcon: "icon-cupcoffeedrinkcafesnack",
          opIcon: "icon-coffee",
          isActive: true
        },

        shopper: {
          name: "Shopper",
          title: "商城",
          unIcon: "icon-shangcheng1",
          opIcon: "icon-shangcheng",
          isActive: false
        },
        my: {
          name: "My",
          title: "我的",
          unIcon: "icon-my1",
          opIcon: "icon-my",
          isActive: false
        }
      }
    };
  },
  created() {
    // hash转换
    let hashes = window.location.hash.split("/");
    let hash = hashes[hashes.length - 1];
    hash = hash.slice(0, 1).toUpperCase() + hash.slice(1);

    // 图标对应路由
    for (let k in this.navBotton) {
      this.navBotton[k].isActive = false;
      // 二级
      if (hash == "Recommend" || hash == "Menu") {
        this.navBotton.home.isActive = true;
      }

      if (hash == this.navBotton[k].name) {
        this.navBotton[k].isActive = true;
      }
    }
  },
  methods: {
    iconActive(item) {
      for (let k in this.navBotton) {
        this.navBotton[k].isActive = false;
      }

      item.isActive = !item.isActive;
    }
  }
};
</script>

<style lang="less" scoped>
@import "../assets/iconfont/font/iconfont.css";
.iconfont {
  display: block;
  font-size: 24px;
  margin-bottom: 5px;
  text-align: center;
  // background-color: #000;
}

/deep/ .van-tabbar-item--active {
  color: #36200c;
}
/deep/ .van-tabbar-item__text {
  color: #36200c;
}
</style>